import React from 'react';
import { PostType } from '../../../context/postsContext';
import { Container, Title, Date, Header } from './styles';
import ReactMarkdown from 'react-markdown';

interface Post {
  data: PostType;
  onClick(): void;
}

const Post: React.FC<Post> = ({
  data,
  onClick,
}) => {
  const { title, date, text } = data;

  return (
    <Container onClick={onClick}>
      <Header>
        <Title>{title}</Title>
        <Date>{date.toDateString()}</Date>
      </Header>
      <ReactMarkdown source={text}/>
    </Container>
  )
}

export default Post;
