import React, { useRef, useEffect, useCallback, createRef, useState } from 'react';
import { TextArea, Container, Button, ButtonContainer, Input } from './styles';
import { useHistory } from 'react-router-dom';
import { usePosts } from '../../context/postsContext';
import { uuid } from 'uuidv4';

const TextEditor: React.FC = () => {
  const [text, setText] = useState('');
  const [title, setTitle] = useState('');

  const history = useHistory();
  const { addPost } = usePosts();

  const handleSave = useCallback((text, title) => {
    const post = {
      id: uuid(),
      title,
      text,
      date: new Date(),
    }
    addPost(post);
    history.push('/');
  },[]);

  return(
    <Container>
      <ButtonContainer>
        <Button save onClick={() => handleSave(text, title)}>publish</Button>
        <Button onClick={() => history.push('/')}>cancel</Button>
      </ButtonContainer>
      <Input
        onChange={event => setTitle(event.target?.value)}
        value={title}
        placeholder="post title"
      />
      <TextArea
        onChange={event => setText(event.target?.value)}
        value={text}
        placeholder="post body in markdown language"
      />
    </Container>
  )
}

export default TextEditor;
