import React, { createContext, useState, useContext, useCallback } from 'react';
import { uuid } from 'uuidv4';

export interface PostType {
  id: string;
  title: string;
  text: string;
  date: Date;
}

interface PostContextData {
  posts: PostType[];
  addPost(post: PostType): void;
}

const PostsContext = createContext<PostContextData>({} as PostContextData);

const PostsProvider: React.FC = ({ children }) => {
  const [posts, setPosts] = useState<PostType[]>([
    {
      id: '1',
      title: 'Teste1',
      text: 'oia só esse post',
      date: new Date(),
    },
    {
      id: '2',
      title: 'Teste2',
      text: 'oia só esse post',
      date: new Date(),
    },
    {
      id: '3',
      title: 'Teste3',
      text: 'oia só esse post',
      date: new Date(),
    },
  ]);

  console.log(posts);

  const addPost = useCallback((post: PostType) => {
    setPosts([...posts, post]);
  }, []);

  return (
    <PostsContext.Provider value={{ posts, addPost }}>
      {children}
    </PostsContext.Provider>
  );
};

function usePosts(): PostContextData {
  const context = useContext(PostsContext);

  if (!context) {
    throw new Error('usePosts must be used within a PostsProvider');
  }

  return context;
}

export { usePosts, PostsProvider };
